 <template>
   <div class='keyboardContainer' ref='wrap'>
     <!-- canvas[SimpleKeyboardSwipe.default]-->
     <div class='simple-keyboard' ref='keyboard'>
    </div>
  </div>
</template>
 <script>
   module.exports={
     props:{
       layout:{
         default  :[],
         required :true,
         type     :Array
       }
     },
     data(){
       return{
         input:'',
         keboard:null,
         swipecanvas:null,
       }
     },
     methods:{
       beforeFirstRender(){
         this.swipecanvas = this.$refs.wrap.firstChild
         this.swipecanvas.addEventListener('mouseup',this.done)
       },
       onChange(input){
         this.input = input
       },
       redraw(){
         return new SimpleKeyboard.default(
           this.$refs.keyboard,
           {
             modules                   :[SimpleKeyboardSwipe.default],
             beforeFirstRender         :this.$nextTick(this.beforeFirstRender),
             onChange                  :this.onChange,
             layout: {
               default                 :this.layout
             },
             physicalKeyboardHighlight :false,
             useMouseEvents            :true
           }
         )
       },
       done(){
         this.$emit('complete',this.input)
         this.keyboard.setInput('')
         this.input = ''
       }
     },
     watch:{
       layout:{
         immediate:true,
         handler(){
           if (this.keyboard){
               this.keyboard.destroy()
               this.swipecanvas.remove()
           }
           this.$nextTick(()=>{
             this.keyboard = this.redraw()
           })
         }
       }
     }
   }
</script>
