 <template>
   <div class='ui action left icon input'>
     <input :type='state.input' autocomplete='new-password' v-model='text'>
     <i :class="['icon',state.password]"></i>
     <button class='ui icon button' @click='toggleState'>
       <i :class="['icon outline', state.button]"></i>
    </button>
  </div>
</template>
 <script>


   module.exports={
     props:{
       value:{
         type:String,
         default:''
       }
     },
     beforeCreate(){
       zxcvbnts.core.zxcvbnOptions.setOptions({
         graphs: zxcvbnts['language-common'].adjacencyGraphs,
         translations: zxcvbnts['language-ja'].translations,
         dictionary: {
           ...zxcvbnts['language-common'].dictionary,
           ...zxcvbnts['language-ja'].dictionary
         }
       })
     },
     computed:{
       text:{
         get(){
           return this.value
         },
         set(value){
           this.$emit('input',value)
         }
       },
       score(){
         return zxcvbnts.core.zxcvbn(
           this.text
         )
       },
       state(){
         return{
           // パスワードが強いかどうか
           password:['meh outline','meh blank outline','smile outline','grin stars outline','laugh squint outline'][this.score.score],
           // パスワードを隠すかどうか
           button:this.readonly ? 'eye' : 'eye slash',
           input :this.readonly ? 'password' : 'text'
         }
       }
     },
     methods:{
       toggleState(){
         this.readonly = !this.readonly
       }
     },
     data(){
       return{
         readonly:false
       }
     }
   }
</script>
