import App from './vue/app.vue';
import Entry from './vue/entry.vue';
import Setting  from './vue/setting.vue';

Vue.use(VueRouter)
Vue.use(VueDatGui)

var routes = [
  { props:true, name:'',        path: '/',          component: App,    redirect:'/entry'},
  { props:true, name:'entry',   path: '/entry',     component: Entry },
  { props:true, name:'setting', path: '/setting',   component: Setting }
]

document.addEventListener('DOMContentLoaded',()=>{
  new Vue({
    el:'main',
    router:new VueRouter({
      routes:routes
    }),
    render:function(h){
      return h(App)
    }
  })
})
